@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, p, button, label, textarea, input, span{
  font-family: 'Open Sans', sans-serif;
}



  


.navbar-wrapper{
    width: 100%;
    background-color: #2F2F2F;
}

.navbar{
    height: 70px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    width: 90%;
    justify-content: center;
    padding: 0;
}

.navbar h1{
    width: 100%;
    margin: 0;
    font-size: 1.33333333rem;
    font-weight: normal;
    color: white;
}


.header-wrapper{
    width: 100%;
    background-color: #F6F6F6;
}

.header{
    margin: 0 auto;
    width: 90%;
    padding: 25px 0 50px 0;
}

.header p {
    width: 100%;
}

.header button {
    border: none;
    padding: 10px 15px 10px 15px;
    background-color: #0067B8;
    color: white;
    font-weight: bold;
    cursor: pointer;
}


@media only screen and (max-width: 960px) {
    .header p {
        width: 100%;
        text-align: justify;
    }
}


.content-wrapper{
    width: 100%;
    background-color: white;
}

.content{
    margin: 0 auto;
    width: 90%;
    padding: 25px 0 30px 0;
}

.content label {
    font-weight: bold;
}

.content input {
    box-sizing: border-box;
    width: 100%;
    border-radius: 0;
    border: solid 1px lightgrey;
    padding: 10px 15px 10px 15px;
}

.content .inputError {
    border: solid 1px red;

}

.spanBox{
    width: 100%;
    margin-bottom: 15px;
}

.content span{
    font-size: 15px;
    font-weight: bold;
    color: red; 
}

.content button {
    border: none;
    padding: 10px 15px 10px 15px;
    background-color: #0067B8;
    color: white;
    font-weight: bold;
    cursor: pointer;
    margin-right: 10px;
    transition: all .2s ease;
}

.content button:hover {
    opacity: 0.8;
}

.content button:disabled {
    background-color: lightgrey;
}

.content .secondaryButton{
    border: solid 1px #0067B8;
    background-color: white;
    color: #0067B8;
}

.content .secondaryButton:hover {
    background-color: #0067B8;
    color: white;
}



.content-wrapper{
    width: 100%;
    background-color: white;
}

.content{
    margin: 0 auto;
    width: 90%;
    padding: 25px 0 30px 0;
}

.content textarea {
    box-sizing: border-box;
    width: 100%;
    border-radius: 0;
    border: solid 1px lightgrey;
    padding: 10px 15px 10px 15px;
    margin-bottom: 15px;
    resize: vertical;
}
.outputArea{
    width: 100%;
    overflow-x: auto;
}

.parentDeepst{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px; 
    border: solid 2px green;
    margin: 5px;
    background-color: #f6f6f6;
}

.parent{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px; 
    border: solid 1px purple;
    background-color: #f6f6f6;
    margin: 5px;
}

.children{
    padding: 10px;   
    border: solid 1px peru;
    display: flex;
    margin-top: 10px;
    
}

.id{
    font-weight: bold;
    color: black;
    width: 100%;
    text-align: center;
    margin: 0;
}
.footer-wrapper{
    width: 100%;
    margin-top: 50px;
    background-color: #f6f6f6;
}

.footer{
    margin: 0 auto;
    width: 90%;
    padding: 10px 0 10px 0;
}

.footer h3{
    width: 100%;
    margin-bottom: 0;
    text-align: center;
}

.iconsBox{
    display: flex;
    justify-content: center;
}

.iconsBox a {
    color: black;
    cursor: pointer;
    margin: 10px;
}

.iconsBox a:hover{
    color: #0067B8;
}
